.pedidoCliente{
    color: white !important;
    border-radius: 25px;
    border: 1px solid whitesmoke
}
.separator{
    border-top: 1px solid grey
}
.eliminarPedido:hover{
    cursor: pointer;
    transform: scale(1.4);
    transition: .5s;
    color: red
}
.separator p{
    margin:0
}