.product{
    flex:1;
    background: whitesmoke;
    border-radius: 25px;
    user-select: none;
    animation: product 1s both;
    animation-delay: .5s;
    border: 1px solid whitesmoke
}
.deleteIcon{
    font-size: 1.3rem !important;
    color:white !important;
}
.deleteIcon:hover{
    cursor: pointer;
    transform: scale(1.3);
    transition: .5s;
    color:red !important
    
}
@keyframes product{
    from{
        transform: scale(0)
    }
    to{
        transform: scale(1) 
    }
}
.border-none{
    border:none
}
.fs14{
    font-size: 5
}
.bodyProduct{
    display:flex;
    flex:1;
}
.img{
    display:flex;
    align-items: center;
    justify-content:center
}
.colorWhite{
    color: white !important
}
.textBold{
    font-weight: bold
}
.productImg{
    height: 125px;
}
.icon{
    color: white;
    cursor:pointer
}
.icon:hover{
    transition: 0.3s;
    transform: scale(1.3);
    color:grey
}
.product-election{
    border-radius: 25px;
    border: 1px solid whitesmoke;
    cursor: pointer;
    transition: .5s !important;
}
.product-election:hover{
    transform: scale(1.1) !important
}
.btnProduct{
    font-size: 11px !important
}
.nombreProducto:hover{
    cursor: pointer;
    color:green !important;
    transition: .5s

}
.timesIcon{
    color: red
}
.arrowDownIcon{
    color: black
}
.timesIcon:hover{
    cursor: pointer
}
.cadena:hover{
    cursor:pointer;
    background: lightgray
}
