.ficha{
    color: white;
    border-radius: 10px;
    display: flex;
    justify-content: space-between
}
.backIcon:hover{
    cursor: pointer;
    transition: .4s;
    transform: scale(1.3)
}
.backIcon{
    color:white
}
.edit{
    color: green !important;
    cursor: pointer;
}
.color-white{
    border-radius: 10px;
    color: white !important;
    border: 1px solid white
}
.color-black{
    border-radius: 10px;
    color: rgb(29, 30, 112) !important;
    border: 1px solid white;
    background: gray
}
.deuda{
    transition: .5s;
    cursor: pointer
}
.danger-zone{
    display: flex;
    justify-content: space-evenly
}
.nav{
    color:white
}
.name{
    color: white;
    text-shadow: 3px 3px 2px black;
    user-select: none
}
.newInfo{
    color:green;
    user-select: none;
    cursor: pointer;
    animation: newInfo 1s infinite alternate;

}
.dataItem{
    user-select: none;
    cursor: pointer;
    animation: delete 1s infinite alternate-reverse;
}
@keyframes delete{
    from{
        background: transparent
    }
    to{
        background: red
    }
}
@keyframes newInfo{
    from{
        transform: scale(2)
    }
    to{
        transform: scale(2.5)
    }
}
@keyframes edit{
    from{
        background: transparent
    }
    to{
        background: green
    }
}
th{
    vertical-align: middle !important
}
td p{
    margin: 0
}
.eliminarValor{
    cursor: pointer;
    color: red;
    transition: .5s;
    text-shadow: 0.5px 0.5px 0.5px  white
}
.valor{
    letter-spacing: .25rem
}